//Creates a react hook to persist data in local storage.

import { useState } from "react";
//import { postProcessData } from "./initialData";

//-------taken from https://usehooks.com/useLocalStorage/ --------------
/**
 * Set-up application state variable to be persisted in local storage
 * @param {String} key - local storage key
 * @param {*} initialValue - initial value when local storage is not available
 */
const useLocalStorage = (key, initialValue) => {
  // State to store our value
  // Pass initial state function to useState so logic is only executed once
  const [storedValue, setStoredValue] = useState(() => {
    try {
      // Get from local storage by key
      const item = window.localStorage.getItem(key);

      // Parse stored json or if none return initialValue
      const itemValue = item ? JSON.parse(item) : initialValue;

      // Optionally post process data
      // const postProcessedItemValue =
      //   key === "initialData" ? postProcessData(itemValue) : null;
      // return postProcessedItemValue || itemValue;

      return itemValue;
    } catch (error) {
      // If error also return initialValue
      console.log(error);
      return initialValue;
    }
  });

  // Return a wrapped version of useState's setter function that ...
  // ... persists the new value to localStorage.
  const setValue = value => {
    try {
      // Allow value to be a function so we have same API as useState
      const valueToStore =
        value instanceof Function ? value(storedValue) : value;
      // Save state
      setStoredValue(valueToStore);
      // Save to local storage
      window.localStorage.setItem(key, JSON.stringify(valueToStore));
    } catch (error) {
      // A more advanced implementation would handle the error case
      console.log(error);
    }
  };

  return [storedValue, setValue];
};
export default useLocalStorage;
// -----------------------------------------------------------------------
